import React from 'react'
import { Link } from "react-router-dom";


const Header = () => {
    return (
        <section className="navbar-area">
        <div className="container">
            <div className="row">
                <div className="col-lg-12">
                    <nav className="navbar navbar-expand-lg">
                       
                    <Link to="/">
                            <img src=".././assets/images/logo.svg" alt="Logo"/>
                    </Link>
                        
                        <button className="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarTwo" aria-controls="navbarTwo" aria-expanded="false" aria-label="Toggle navigation">
                            <span className="toggler-icon"></span>
                            <span className="toggler-icon"></span>
                            <span className="toggler-icon"></span>
                        </button>

                        <div className="collapse navbar-collapse sub-menu-bar" id="navbarTwo">
                        
                            <ul className="navbar-nav m-auto">
                           
                                <li className="nav-item active"> <Link className="page-scroll" to="/">home</Link></li>
                                        <li className="nav-item"><Link className="page-scroll" to="/portfolio">Portfolio</Link></li>
                                        <li className="nav-item"><Link className="page-scroll" to="/pricing">Pricing</Link></li>
                                        <li className="nav-item"><Link className="page-scroll" to="/about">About</Link></li>
                                        <li className="nav-item"><Link className="page-scroll" to="/team">Team</Link></li>
                                        <li className="nav-item"><Link className="page-scroll" to="/contact">Contact</Link></li>
                            </ul>
                           
                        </div>
                        
                        <div className="navbar-btn d-none d-sm-inline-block">
                            <ul>
                                <li><a className="solid" href="#">Download</a></li>
                            </ul>
                        </div>
                    </nav> 
                </div>
            </div> 
        </div> 
    </section>

    )
}

export default Header
